const getStations = () => {
    return stations
}

// 获取路段图标点
// isAdd true则不清空原marker
const getRoadMarker = (map, markers, callback) => {
  // if(isAdd){
  //   this.mapReset()
  // }
  // let stations = await getStations(3)
  let stations = getStations()
  console.log('我是站点信息',stations.length)
  for(let i = 0; i < stations.length; i++){
    if(stations[i].longitude > 100 && stations[i].latitude > 20){
      // eslint-disable-next-line no-undef
      let marker = new AMap.Marker({
        markid: stations[i].id,
        map: map,
        topWhenClick: true, // 鼠标点击时marker是否置顶，默认false
        animation: 'AMAP_ANIMATION_DROP',
        // eslint-disable-next-line no-undef
        position: new AMap.LngLat(
          stations[i].longitude,
          stations[i].latitude
        ),
        content: stations[i].stat.idle > 5 ? 
          `<div style="position:'relative';width:35px;height:35px;">
            <img src='${haroad5}' width='35px' height='35px'></img>
            <span style="position:absolute;top:0;left:0;right:0;bottom:0;z-index:12;color:#fff;text-align: center;line-height:30px">${stations[i].stat.idle}</span>
          </div>`
          : (stations[i].stat.idle > 0 ? 
            `<div style="position:'relative';width:35px;height:35px;">
              <img src='${haroad1}' width='35px' height='35px'></img>
              <span style="position:absolute;top:0;left:0;right:0;bottom:0;z-index:12;color:#fff;text-align: center;line-height:30px">${stations[i].stat.idle}</span>
            </div>`
            : `<div style="position:'relative';width:35px;height:35px;">
              <img src='${haroad0}' width='35px' height='35px'></img>
              <span style="position:absolute;top:0;left:0;right:0;bottom:0;z-index:12;color:#fff;text-align: center;line-height:30px">${stations[i].stat.idle}</span>
              </div>`),
        // eslint-disable-next-line no-undef
        offset: new AMap.Pixel(-17, -35),
        angle: 0,
        clickable:true
      });
      marker.on("click", async () => {
        callback(stations[i])
        // this.roadDetailsData = [];
        // if(this.showRoadDetails){
        //   this.showRoadDetails = false;
        //   setTimeout(() => {
        //     this.showRoadDetails = true;
        //   }, 320);
        // }else{
        //   this.showRoadDetails = true;
        // }
        // // 保存路段详情
        // this.roadDetails = stations[i]
        // // 获取路段泊位信息
        // let roadDetails = await this.getRoadDetails(stations[i].id);
        // this.roadDetailsData = roadDetails
        // console.log("路段详情", stations[i])
      });
      // console.log('marker1111',marker);
      markers.push(marker)
    }
  }
  map.add(markers)
}

